dana}.portrait{height:150px;background:var(--accent-light);outline:1px solid var(--background);outline-offset:-4px;box-shadow:2px 2px 0px var(--accent-light)}.content-box{margin:2vmin;width:60vmin}.content-box header{display:flex;justify-content:space-between;align-items:flex-end;margin-bottom:1vmin}.content-box header button{font-family:verdana}.content-box h1{margin:0;width:min-content;font-size:50px;line-height:50px;font-family:"Lemon Yellow Sun";letter-spacing:1px}.content-box nav{background:var(--accent-light);outline:1px solid #fff;outline-offset:-4px;padding:8px;display:block;margin:0;font-size:20px;line-height:20px;font-family:"Lola Sans";text-transform:uppercase;display:flex;justify-content:space-around;box-shadow:2px 2px 0px var(--accent-light)}.content-box nav a{text-decoration:none;color:var(--text-primary);transition:all 2s ease-in-out}.content-box nav .mark{position:relative;background:none;animation:none;transition:all 2s ease-in-out}.content-box nav .mark:after{border:.25em solid #cc96ff;border-top:none;border-radius:1.5em 1em 2em 1.5em;bottom:-0.1em;content:"";left:-0.3em;position:absolute;right:-0.3em;top:-0.1em;transition:all 2s ease-in-out}.content-box nav .mark:before{border-top:.25em solid #cc96ff;border-radius:.5em;content:"";height:1em;left:0;position:absolute;top:-0.3em;transform:rotate(-10deg);width:2em;transition:all 2s ease-in-out}.content-box nav .active-link{transition:all 4s ease-in-out;background:none;animation:none}.content-box .text-content{font-family:verdana;padding:6px;border:2px solid var(--accent-light);box-shadow:2px 2px 0px var(--accent-light);text-align:justify}@-webkit-keyframes move{from{background-position:2px 19px}to{background-position:500px 19px}}@keyframes move{from{background-position:2px 19px}to{background-position:500px 19px}}footer{font-size:11px;display:flex;justify-content:flex-end}footer a{margin:0px 4px}.scooby-shrine{display:flex;justify-content:center;align-items:center}.scooby-shrine img{width:30%;height:30